import { Fragment } from "react";
import { Disclosure, Menu, Transition } from "@headlessui/react";
import {
  MagnifyingGlassIcon,
  ShoppingBagIcon,
} from "@heroicons/react/24/outline";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { selectItems } from "../cart/cartSlice";
import { selectLoggedInUser } from "../auth/authSlice";

const navigation = [
  { name: "Home", link: "/", user: true },
  { name: "Manage Products", link: "/admin", admin: true },
  { name: "Orders", link: "/admin/orders", admin: true },
];
const userNavigation = [
  { name: "My Profile", link: "/profile" },
  { name: "My Orders", link: "/orders" },
  { name: "Sign out", link: "/logout" },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

function NavBar({ children }) {
  const items = useSelector(selectItems);
  const user = useSelector(selectLoggedInUser);

  return (
    <div className="min-h-full">
      <Disclosure as="nav" className="bg-white border border-gray-300">
        {/* desktop nav */}
        <DesktopNav items={items} user={user} />
      </Disclosure>

      <main>
        <div className="mx-auto max-w-7xl py-6 sm:px-6 lg:px-8">{children}</div>
      </main>
    </div>
  );
}

export default NavBar;

function DesktopNav({ user, items }) {
  return (
    <div className="mx-auto px-4 sm:px-6 lg:px-8">
      <div className="flex h-16 items-center justify-between">
        {/* logo */}
        <div className="flex items-center">
          <div className="flex-shrink-0">
            <Link to="/">
              <img
                className="h-8 w-8"
                src="/ecommerce.png"
                alt="Your Company"
              />
            </Link>
          </div>
          <div className=" md:block">
            <div className="ml-10 flex items-baseline space-x-4">
              {navigation.map((item) =>
                item[user?.role] ? (
                  <Link
                    key={item.name}
                    to={item.link}
                    className="text-gray-500 hover:bg-gray-100 hover:text-black rounded-3xl px-3 py-2 text-sm font-medium"
                    aria-current={item.current ? "page" : undefined}
                  >
                    {item.name}
                  </Link>
                ) : null
              )}
            </div>
          </div>
        </div>
        {/* Search */}
        <div className="flex items-center bg-neutral-50 px-4 w-[40%] md:w-[45%] max-w-sm h-10  rounded-full">
          <input
            type="text"
            placeholder="Search"
            className="w-full bg-transparent border-none outline-none ring-0 focus:ring-0 focus:outline-none text-gray-700"
          />
          <a href="#" className="text-gray-400 hover:text-gray-500">
            <MagnifyingGlassIcon aria-hidden="true" className="ml-2 size-6" />
          </a>
        </div>
        {/* cart */}
        <div className="flex items-center justify-center">
          {/* Cart */}
          <Link to="/cart">
            <button
              type="button"
              className="relative ml-4 mr-6 flow-root lg:ml-6"
            >
              <ShoppingBagIcon
                aria-hidden="true"
                className="size-6 shrink-0 text-gray-400 group-hover:text-gray-500"
              />
              {items.length > 0 && (
                <span className="absolute -top-4 -right-2 inline-flex items-center justify-center w-5 h-5 text-xs font-medium text-red-700 bg-red-50 rounded-full ring-1 ring-inset ring-red-600/10">
                  {items.length}
                </span>
              )}
            </button>
          </Link>

          {/* Profile dropdown */}
          <Menu as="div" className="">
            <div>
              <Menu.Button className="flex items-center rounded-full bg-white focus:outline-none focus:ring-0 focus:ring-black focus:ring-offset-1 focus:ring-offset-gray-100">
                <span className="sr-only">Open user menu</span>
                <img
                  className="h-8 w-8 rounded-full"
                  src={user?.imageUrl}
                  alt=""
                />
              </Menu.Button>
            </div>
            <Transition
              as={Fragment}
              enter="transition ease-out duration-100"
              enterFrom="transform opacity-0 scale-95"
              enterTo="transform opacity-100 scale-100"
              leave="transition ease-in duration-75"
              leaveFrom="transform opacity-100 scale-100"
              leaveTo="transform opacity-0 scale-95"
            >
              <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                {userNavigation.map((item) => (
                  <Menu.Item key={item.name}>
                    {({ active }) => (
                      <Link
                        to={item.link}
                        className={classNames(
                          active ? "bg-gray-100" : "",
                          "block px-4 py-2 text-sm text-gray-700"
                        )}
                      >
                        {item.name}
                      </Link>
                    )}
                  </Menu.Item>
                ))}
              </Menu.Items>
            </Transition>
          </Menu>
        </div>
      </div>
    </div>
  );
}
