import axios from "axios";

export function fetchProductById(id) {
  return axios.get(`/products/${id}`).then((res) => ({ data: res.data }));
}

export function createProduct(product) {
  return axios.post("/products", product).then((res) => ({ data: res.data }));
}

export function updateProduct(update) {
  return axios
    .patch(`/products/${update.id}`, update)
    .then((res) => ({ data: res.data }));
}

export function fetchProductsByFilters(filter, sort, pagination, admin) {
  const params = new URLSearchParams();

  for (let key in filter) {
    const values = filter[key];
    if (values.length) params.append(key, values[values.length - 1]);
  }
  for (let key in sort) params.append(key, sort[key]);
  for (let key in pagination) params.append(key, pagination[key]);
  if (admin) params.append("admin", "true");

  return axios.get(`/products?${params.toString()}`).then((res) => ({
    data: { products: res.data, totalItems: +res.headers["x-total-count"] },
  }));
}

export function fetchCategories() {
  return axios.get("/categories").then((res) => ({ data: res.data }));
}

export function fetchBrands() {
  return axios.get("/brands").then((res) => ({ data: res.data }));
}
